import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Button from 'components/Button/Button';

const StyledMenuWrapper = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
  width: 100%;
  transform: translateX(${({ menuIsOpen }) => (menuIsOpen ? '0px' : '100%')});
  background-color: ${({ theme }) => theme.color.white};
  z-index: 90;
  transition: transform 0.2s ease-in-out;

  ${({ theme }) => theme.mq.desktop} {
    display: none;
  }
`;

const StyledInnerWrapper = styled.ul`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 50%;
  list-style: none;
`;

const StyledLink = styled.li`
  position: relative;
  width: 100%;
  padding: 4px;
  text-align: center;
  opacity: ${({ menuIsOpen }) => (menuIsOpen ? '1' : '0')};
  font-size: ${({ theme }) => theme.fontSize.mobile.s};
  font-weight: ${({ theme }) => theme.fontWeight.bold};
  cursor: pointer;
  transition: opacity 0.25s ease-in-out 0.3s, background-color 0.25s ease-in-out;

  &:hover {
    background-color: ${({ theme }) => theme.color.lightBlue};
  }

  ${({ theme }) => theme.mq.tablet} {
    font-size: ${({ theme }) => theme.fontSize.tablet.m};
  }
`;

const GetCurrentOfferWrapper = styled.div`
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 8rem;
  background-color: hsla(289,9%,42%, .5);
  transform: translateY(${({ menuIsOpen }) => (menuIsOpen ? '-5.5rem' : '100%')});
  transition: transform 0.2s ease-in-out 0.4s;
`;

const MenuItems = ['O nas', 'Usługi', 'Produkty', 'Kontakt'];

const MobileMenu = ({ menuIsOpen }) => (
  <StyledMenuWrapper menuIsOpen={menuIsOpen}>
    <StyledInnerWrapper>
      {MenuItems.map(item => (
        <StyledLink key={item} menuIsOpen={menuIsOpen}>
          {item}
        </StyledLink>
      ))}
    </StyledInnerWrapper>
    <GetCurrentOfferWrapper menuIsOpen={menuIsOpen}>
      <Button menu>Pobierz cennik</Button>
    </GetCurrentOfferWrapper>
  </StyledMenuWrapper>
);

MobileMenu.propTypes = {
  menuIsOpen: PropTypes.bool.isRequired,
};

export default MobileMenu;
